.history_page {
    overflow-x: auto;

    & h2,
    h4 {
        text-align: center;
        margin: 23px;
        text-transform: uppercase;
        letter-spacing: 1.2px;
    }

    & table {
        margin: auto;
        width: 100%;
    }

    & table,
    th,
    tr,
    td {
        border: 1px solid #ddd;
        border-collapse: collapse;
    }
}

th,
td {
    text-align: center;
    padding: 10px;
    text-transform: capitalize;
}

table button {
    color: rgb(3, 155, 205);
}